body,
html {
    width: 100%;
    height: 100%;
}

body {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
        "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}

h1,h2,h3,h4,h5,h6,p{
    margin: 0px;
    padding: 0;
}

header {
    overflow:hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    box-shadow: 0px 2px 30px rgba(0, 0, 0, 0.089);
    gap: 150px;
}

li {
    list-style-type: none;
}

.nav ul {
    display: flex;
    margin: 10px;
    padding: 10px;
}

.nav ul li {
    margin: 0 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 6px;
}

.nav ul li svg{
    fill:#282c3f;
}

.nav ul a {
    color: rgb(0, 0, 0);
    text-decoration: none;
}




.restaurant-card{
    width: 250px;
    /* overflow: hidden; */
    /* box-shadow: 1px 1px 20px rgba(0, 0, 0, 0.174); */
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    gap: 6px;
    cursor: pointer;
}

.restaurant-card :not(.card-img,img){
    margin: 0px 6px;
}

.restaurant-card img{
    width: 100%;
}

.card-img{
    height: 145px;
    overflow: hidden;
    border-radius: 10px;
    box-shadow: 1px 1px 20px rgba(0, 0, 0, 0.174)
}


.card-rating{
    padding: 2px 4px;
    border-radius: 4px;
    background-color:green;
    width: max-content;
    color: white;
    font-size: small;
}

.card-cuisines{
    color: rgb(83, 83, 83);
    font-size: small;
    margin-bottom: 10px;
    padding: 4px 0px;
}

.r-list{
    margin: 100px;
    margin-top: 50px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 30px;
}

.restaurant-card:hover{
    animation: card-hover 200ms ease-in-out forwards;
}



@keyframes card-hover {
    100%{
        transform: scale(0.95);
    }
}


footer{
    text-align: center;
    padding: 10px;
    color: purple;
    font-weight: 600;
}

footer p{
    margin-bottom: 20px;
}

@media only screen and (max-width: 600px) {
    .nav {
        display: none;
    }

    .restaurant-card{
        width: 400px;
    }
}
@media only screen and (max-width: 760px) {
    .r-list{
        margin: 50px 10px;
    }
}